* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.track {
  width: 100%;
  display: flex;
  border-bottom: 1px solid #90bfb3;
  padding: 4px;
  height: 48px;
}

.track.active {
  background: #3dccfb;
}

.track-image-wrapper {
  flex: 0 0 auto;
  height: 100%; 
}

.track-image {
  height: 100%;
  display: block;
  max-width: 100%;
}

.track-play-btn-wrapper {
  margin-left: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.track-play-btn {
  padding: 5px;
  margin: 0;
  width: 30px;
  height: 30px;
  background: transparent;
  border: 2px solid #bbb;
  color: #bbb;
  overflow: hidden;
  cursor: pointer;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.track-play-btn.active {
  color: #fff;
  border-color: #fff;
}

.track-title {
  overflow: hidden;
  flex: 1 1 auto;
  padding: 0 5px; 
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
}

.track-name {
  font-weight: 700;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.track-artist-name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


.progress-slider {
  width: 100%;
  height: 10px;
  cursor: pointer;
  transition: all .2s linear;
}

.progress-slider:hover .progress-slider_handler {
  opacity: 1;
}

.progress-slider_line {
  width: 100%;
  height: 100%;
  background: #bbb;
  position: relative;
}

.progress-slider_filler {
  background: #3dccfb;
  height: 100%;
}

.progress-slider_handler {
  border-radius: 50%;
  background: #00c6c6;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  transition: opacity .2s linear;
}
.volume-control {
  display: flex;
  flex-direction: column;
  height: 100px;
  width: 42px;
  position: absolute;
  top: -10px;
  left: 50%;
  -webkit-transform: translate(-50%,-100%);
          transform: translate(-50%,-100%);
  background: #333;
  border: 1px solid #222;
}

.volume-control-btn {
  flex: 1 0 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3dccfb;
}

/*.volume-control-btn {
  padding: 0;
  flex: 0;
  color: #3dccfb;
  background: transparent;
  border: none;
  cursor: pointer;
}*/

.volume-control-btn:active {
  color: #fff;
}

.volume-control-slider {
  padding: 16px;
  width: 100%;
  position: relative;
  flex: 1 0 auto;
  cursor: pointer;
}

.volume-control-slider:before {
  content: '';
  background: #fff;
  height: 100%;
  width: 4px;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.volume-control-slider-filler {
  position: absolute;
  background: #3dccfb;
  bottom: 0;
  left: 50%;
  width: 4px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.player-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  position: fixed;
  bottom:0;
}

.player-wrapper-hidden {
  display: none;
}

.player {
  width: 100%;
  height: 48px;
  min-width: 320px;
  background: #333;
  display: flex;
}

.player-section-sm {
  flex: 0 1 48px;
  padding: 0 5px;
  border-right: 1px solid #454545;
  display: flex;
  justify-content: center;
  align-items: center;
}

.player-section-lg {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 5px;
}

.player-full-btn {
  margin: 0;
  border: 0;
  height: 100%;
  width: 100%;
  padding: 0;
  background: transparent;
  color: #3dccfb;
  cursor: pointer;
}

.player-track {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}

.player-controls-wrapper {
  display: flex;
  align-self: center;
}

.player-controls {
  flex: 1 0;
  text-align: center;
  padding: 0 20px;
}

.player-control-volume {
  display: none;
  position: relative;
}

.player-control-btn {
  margin: 0;
  border: 0;
  padding: 4px;
  background: transparent;
  color: #3dccfb;
  cursor: pointer;
}

.player-control-btn:active {
  color: #fff;
}

.player-control-btn-volume:focus {
  color: #fff;
}

.player-track-info {
  display: flex;
  color: #3dccfb;
  min-width: 0;
  font-size: 12px;
  flex: 1 1 auto;
}

.player-track-progress-slider {
  flex: 1 1 auto;
  display: none;
}

.player-track-info-title {
  flex: 0 0 65%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.player-track-info-duration {
  flex: 0 0 auto;
  margin-left: auto;
}


@media screen and (min-width: 500px) {
  .player {
    max-width: 600px;
    padding: 0 10px
  }

  .player-control-volume {
    display: block;
    flex: 1 0;  }

  .player-section-lg {
    flex-direction: row;    
  }

  .player-section-sm {
    padding-left: 0;
    padding-right: 10px;
  }

  .player-track {
    padding: 5px 5px 5px 0;
  }
  
  .player-track-progress-slider {
    display: block;
  }

  .player-controls-wrapper {
    padding-left: 10px;
    margin-left: auto;
    height: 100%;
    align-items: center;
    border-left: 1px solid #454545;
    flex: 0 0 148px;
  }

  .player-controls {
    padding: 0 5px;
  }
}
.full-player-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #555;
}

.full-player {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: flex-end;
}

.full-player-close-btn-wrapper {
  margin-bottom: auto;
  flex: 0 0 48px;
}

.full-player-close-btn {
  margin: 0;
  padding: 0;
  background: transparent;
  color: #fff;
  border: none;
  height: 100%;
  width: 48px;
  cursor: pointer;
}

.full-player-playlist-wrapper {
  flex: 1 0;
  border: 1px solid #454545;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: #333;
}
.full-player-playlist-wrapper:before,
.full-player-playlist-wrapper:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  /*height: 1px;*/
  box-shadow: 0 0 10px 3px #000;
  z-index: 999;
}

.full-player-playlist-wrapper:before {
  top: 0;
}

.full-player-playlist-wrapper:after {
  bottom: 0;
}

.full-player-controls-wrapper {
  flex: 0 0 100px;
  border-top: 1px solid #454545;
  display: flex;
  justify-content: center;
}

.full-player-controls {
  color: #fff;
  display: flex;
  flex: 0 0 300px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: stretch;
  align-content: space-between;
}

.full-player-volume-wrapper {
  display: flex;
  flex: 1 0 40%;
  align-items: stretch;
}

.full-player-btn-wrapper {
  display: flex;
  justify-content: center;
  flex: 0 0 50%;
}

.full-player-btn {
  flex-grow: 1;
  margin: 0;
  padding: 0;
  background: transparent;
  border: none;
  color: #fff;
  cursor: pointer;
}

.full-player-track-info-wrapper {
  padding: 10px;
}

.full-player-track-info {
  display: flex;
  flex-direction: column;
}

.full-player-track-img {
  flex: 0 0 auto;
  width: 128px;
  align-self: center;
  border: 1px solid #454545;
  margin-bottom: 5px;
}

.full-player-track-data-title {
  margin-bottom: 5px;
  color: #fff;
  font-size: 14px;
  text-align: center;
}

.full-player-track-data-row {
  color: #fff;
  display: flex;
  align-items: center;  
}

.full-player-track-current-time,
.full-player-track-duration {
  color: #fff;
  font-size: 12px;
}

.full-player-track-time-progress-slider {
  flex: 1 0 auto;
  padding: 0 10px;
}

@media screen and (min-width: 500px) {
  .full-player-track-info {
    flex-direction: row;
  }

  .full-player-track-img {
    flex: 0 0 128px;
  }

  .full-player-track-data {
    flex: 1 0 auto;
    align-self: center;
    padding-left: 10px;
  }

  .full-player-track-data-title {
    flex: 1 0;
    margin-bottom: 20px;
    font-size: 16px;
  }

  .full-player-track-data-row {
    flex: 1 0;
  }
}

@media screen and (min-width: 700px) {
  .full-player-track-img {
    flex: 0 0 200px;
  }

  .full-player-track-data-title {
    font-size: 18px;
  }
}
.volume-control-full {
  display: flex;
  flex: 1 1;
  align-items: center;
}

.volume-control-full-btn-wrapper {
  flex: 0 0 25%;
  justify-content: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.volume-control-full-btn {
  cursor: pointer;
  padding: 5px;
}

.volume-control-full-slider {
  flex: 1 0 auto;
  height: 15px;
  position: relative;
  cursor: pointer;
}

.volume-control-full-slider:before {
  content: '';
  background: #bbb;
  width: 100%;
  height: 6px;
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.volume-control-full-slider-filler {
  position: absolute;
  background: #fff;
  left: 0;
  top: 50%;
  height: 6px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.playlist {
  color: #fff;
  overflow: auto;
  height: 100%;
  /*background: #444;*/
  z-index: -1;
}
.search {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.search-input {
  flex: 1 1;
  border: 1px solid #90bfb3;
  padding: 3px;
}

.search-btn {
  border: 1px solid #90bfb3;
  border-left: none;
  background: #90bfb3;
  color: #fff;
  cursor: pointer;
}
